import styled from 'styled-px2vw';
/**
 * 消息列表
 */
export const MsgBox = styled.div`
  padding-left: 32px;
`;

export const MsgItem = styled.div`
  height: 175px;
  border-bottom: 1px solid #ccc;
  display: flex;
  align-items: center;
`;
export const MsgImg = styled.img`
  width: 96px;
  height: 96px;
  border-radius: 50%;
`;
export const MsgInfoBox = styled.div`
  padding-left: 24px;
  padding-right: 32px;
  width: 100%;
`;

export const MsgInfoTop = styled.div`
  display: inline-flex;
  justify-content: space-between;
  font-size: 30px;
  width: 100%;
  height: 40px;
  align-items: center;
`;

export const MsgTitle = styled.div`
  font-weight: bold;
`;
export const MsgDate = styled.div`
  color: rgba(60, 60, 67, 0.6);
  display: inline-flex;
  align-items: center;
`;
export const MsgInfoBottom = styled.div`
  font-size: 26px;
  color: rgba(60, 60, 67, 0.6);
  line-height: 36px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  word-break: break-all;
`;
