import React from 'react';
import { Icon } from 'antd-mobile';
// import { useHistory } from 'react-router-dom';
import { useQuery } from '@apollo/client';
import { QUERY_INFORMATION } from '@/helpers/APIHelper';
import {
  MsgBox,
  MsgItem,
  MsgImg,
  MsgInfoBox,
  MsgInfoTop,
  MsgTitle,
  MsgDate,
  MsgInfoBottom,
} from './style';
import iconBg from '../UserCenter/images/bg.png';

const Msg: React.FC = () => {
  const { data = {} } = useQuery(QUERY_INFORMATION);
  // const history = useHistory();

  console.log(data);
  const { getUserMessages } = data;
  return (
    <MsgBox>
      {getUserMessages?.map((item: { [key: string]: string }) => {
        const { content, date, title } = item || {};
        return (
          <MsgItem>
            <MsgImg src={iconBg} />
            <MsgInfoBox>
              <MsgInfoTop>
                <MsgTitle>{title}</MsgTitle>
                <MsgDate>
                  {date}
                  <Icon type="right" color="#8A8A8E" size="sm" />
                </MsgDate>
              </MsgInfoTop>
              <MsgInfoBottom>{content}</MsgInfoBottom>
            </MsgInfoBox>
          </MsgItem>
        );
      })}
    </MsgBox>
  );
};

export default Msg;
